@-webkit-keyframes wave {
  50%,
  75% {
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
  }
  80%,
  100% {
    opacity: 0;
  }
}
@keyframes wave {
  50%,
  75% {
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
  }
  80%,
  100% {
    opacity: 0;
  }
}
.animation-wave {
  display: flex;
  padding: 60px;
  background: #222;
}
.animation-wave .dot {
  position: relative;
  width: 2em;
  height: 2em;
  margin: 0.8em;
  border-radius: 50%;
}
.animation-wave .dot::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  -webkit-animation: wave 2s ease-out infinite;
          animation: wave 2s ease-out infinite;
}
.animation-wave .dot:nth-child(1) {
  background: #7ef9ff;
}
.animation-wave .dot:nth-child(1)::before {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
.animation-wave .dot:nth-child(2) {
  background: #89cff0;
}
.animation-wave .dot:nth-child(2)::before {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}
.animation-wave .dot:nth-child(3) {
  background: #4682b4;
}
.animation-wave .dot:nth-child(3)::before {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}
.animation-wave .dot:nth-child(4) {
  background: #0f52ba;
}
.animation-wave .dot:nth-child(4)::before {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}
.animation-wave .dot:nth-child(5) {
  background: #000080;
}
.animation-wave .dot:nth-child(5)::before {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
@-webkit-keyframes landIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes landIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.animation-landIn {
  padding: 60px;
  background: #222;
}
.animation-landIn .landIn {
  display: flex;
  flex-wrap: wrap;
  line-height: 1.8;
  color: white;
  font-family: Lora, serif;
  font-size: 2em;
  font-weight: 600;
  white-space: pre;
}
.animation-landIn .landIn span {
  -webkit-animation: landIn 0.8s ease-out both;
          animation: landIn 0.8s ease-out both;
}
@-webkit-keyframes fadeIn {
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes fadeIn {
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes slideLeft {
  to {
    left: -6%;
    opacity: 1;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
@keyframes slideLeft {
  to {
    left: -6%;
    opacity: 1;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
@-webkit-keyframes slideRight {
  to {
    right: -6%;
    opacity: 1;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
@keyframes slideRight {
  to {
    right: -6%;
    opacity: 1;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
.animation-reveal {
  display: flex;
  justify-content: center;
  padding: 60px;
  background: #222;
}
.animation-reveal .reveal {
  position: relative;
  display: flex;
  color: #6ee1f5;
  font-size: 2em;
  font-family: Raleway, sans-serif;
  letter-spacing: 3px;
  text-transform: uppercase;
  white-space: pre;
}
.animation-reveal .reveal span {
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation: fadeIn 2.4s forwards;
          animation: fadeIn 2.4s forwards;
}
.animation-reveal .reveal::before,
.animation-reveal .reveal::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  width: 2px;
  height: 100%;
  background: white;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.animation-reveal .reveal::before {
  left: 50%;
  -webkit-animation: slideLeft 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) forwards;
          animation: slideLeft 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) forwards;
}
.animation-reveal .reveal::after {
  right: 50%;
  -webkit-animation: slideRight 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) forwards;
          animation: slideRight 1.5s cubic-bezier(0.7, -0.6, 0.3, 1.5) forwards;
}
.animation-fill {
  padding: 60px;
  background: #222;
}
.animation-fill ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  list-style-type: none;
}
.animation-fill ul li {
  padding: 6px 0;
}
.animation-fill ul li a {
  --fill-color: #198CE6;
  position: relative;
  display: block;
  padding: 4px 0;
  font-family: Raleway, sans-serif;
  font-size: 3em;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-text-stroke: 2px var(--fill-color);
  background: -webkit-gradient(linear, left top, right top, from(var(--fill-color)), to(var(--fill-color)));
  background: linear-gradient(90deg, var(--fill-color) 0%, var(--fill-color) 100%);
  background-size: 0;
  background-position: left;
  background-repeat: no-repeat;
  color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-transition: 0.5s linear;
  transition: 0.5s linear;
}
.animation-fill ul li a:hover {
  background-size: 100%;
}
@-webkit-keyframes pulse {
  from {
    box-shadow: 0 0 0 0 red;
  }
}
@keyframes pulse {
  from {
    box-shadow: 0 0 0 0 red;
  }
}
.animation-test {
  padding: 60px;
  background: #222;
}
.animation-test .button {
  height: 30px;
  width: 80px;
  line-height: 30px;
  text-align: center;
  background-color: red;
  box-shadow: 0 0 0 1em transparent;
  border-radius: 2px;
  cursor: pointer;
}
.animation-test .button:hover {
  -webkit-animation: pulse 2s;
          animation: pulse 2s;
}
